<template>
  <div>
    <!-- 顶部 -->
    <van-search
      left-icon="https://jp.juancdn.com/jpwebapp_v1/images_v1/icon/search.png?f4e07c7d-1&sv=449ce9ee"
      placeholder="搜索商品"
      @focus="getSearch"
    />

    <van-row>
      <!-- 左边分类 -->
      <van-col span="6">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item
            :title="item"
            v-for="(item,index) in $store.state.classify.leftClass"
            :key="index"
          />
        </van-sidebar>
      </van-col>

      <!-- 右边内容 -->
      <van-col span="18">
        <div
          class="box-container"
          v-for="(el,i) in $store.state.classify.rightValue[activeKey]"
          :key="i"
        >
          <img :src="el.url" @click="$router.push({path:`/shoppingClass`,query:{ text: el.name}})" />
          <div class="icon-box" v-show="el.bool" :style="el.bgColor">{{el.icon}}</div>
          <p class="name">{{el.name}}</p>
        </div>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeKey: 0
    };
  },
  methods:{
     getSearch() {
      this.$store.state.bool = false;
      this.$router.push("/search");
    }
  },
  created(){
    if (this.$route.query.ify) {
       this.activeKey = this.$route.query.ify
    }
  }
};
</script>

<style lang="scss" scoped>
p {
  padding: 0;
  margin: 0;
}
.van-cell {
  border: 1px solid #bbb;
  border-radius: 5px;
}
.van-icon {
  margin-left: 6px;
  width: 24px;
  height: 24px;
}
.van-icon img {
  width: 100%;
  height: 100%;
}
.van-sidebar {
  padding-left: 20px;
}
.van-sidebar-item {
  height: 45px;
  line-height: 45px;
  position: relative;
  border-bottom: 1px solid #ebebeb;
  cursor: pointer;
  padding: 0;
}
.van-sidebar-item--select {
  color: #ff464e;
}
.van-col--18 {
  margin-top: 10px;
}
.box-container {
  width: 33.33333%;
  display: inline-block;
  padding: 0 5%;
  box-sizing: border-box;
  text-align: center;
  position: relative;
  margin-bottom: 16px;
}
.box-container img {
  width: 100%;
}
p.name {
  font-size: 12px;
  color: #333;
}
.icon-box {
  background: #5698f3;
  position: absolute;
  width: 30px;
  height: 30px;
  line-height: 30px;
  font-size: 10px;
  top: -8px;
  text-align: center;
  border-radius: 50%;
  right: 4px;
  color: #fff;
}
</style>